import { Component, OnInit } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFirestoreCollection, AngularFirestoreDocument, AngularFirestore } from '@angular/fire/firestore';
import { Day } from 'src/app/models/day';
import { Observable } from 'rxjs/internal/Observable';
import * as moment from 'moment';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.scss']
})
export class FooterComponent implements OnInit {

  user: firebase.User;

  private dayCollection: AngularFirestoreCollection<Day>;
  days: Observable<Day[]>;

  private todayDoc: AngularFirestoreDocument<Day>;
  today: Day;

  constructor(
    private auth: AngularFireAuth,
    private db: AngularFirestore
  ) {
    this.dayCollection = db.collection<Day>('days');
    this.days = this.dayCollection.valueChanges();

    this.todayDoc = db.doc<Day>('days/' + moment().format(environment.dateformat));
    this.todayDoc.valueChanges().subscribe(val => {
      if (val) {
        console.log(val);
        this.today = val;
      } else {
        let t = new Day();
        this.dayCollection.doc(moment().format(environment.dateformat)).set(Object.assign({}, t));
      }
    });

    this.auth.auth.onAuthStateChanged(user => {
      this.user = user;
    });
  }

  ngOnInit() {
  }


  add(key: string) {
    this.today[key]++;
    this.todayDoc.update(this.today);
  }
}
