import { Component, OnInit } from '@angular/core';
import { AngularFirestoreCollection, AngularFirestore, AngularFirestoreDocument } from '@angular/fire/firestore';
import { Day } from 'src/app/models/day';
import { Observable } from 'rxjs';
import * as moment from 'moment';
import { environment } from 'src/environments/environment';


@Component({
  selector: 'app-navigation',
  templateUrl: './navigation.component.html',
  styleUrls: ['./navigation.component.scss']
})
export class NavigationComponent implements OnInit {

  private dayCollection: AngularFirestoreCollection<Day>;
  days: Observable<Day[]>;

  private todayDoc: AngularFirestoreDocument<Day>;
  today: Day;

  constructor(
    private db: AngularFirestore
  ) {
    this.dayCollection = db.collection<Day>('days');
    this.days = this.dayCollection.valueChanges();

    this.todayDoc = db.doc<Day>('days/' + moment().format(environment.dateformat));
    this.todayDoc.valueChanges().subscribe(val => {
      if (val) {
        console.log(val);
        this.today = val;
      } else {
        let t = new Day();
        this.dayCollection.doc(moment().format(environment.dateformat)).set(Object.assign({}, t));
      }
    });
  }

  ngOnInit() {
  }

  add() {
    this.today.visitors++;
    this.todayDoc.update(this.today);
  }
}
