import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './components/login/login.component';
import { PageNotFoundComponent } from './components/page-not-found/page-not-found.component';
import { LogComponent } from './components/log/log.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { AppInternComponent } from './components/app-intern/app-intern.component';
import { AngularFireAuthGuard } from '@angular/fire/auth-guard';''

const routes: Routes = [
  { path: 'login', component: LoginComponent },

  {
    path: 'app',
    component: AppInternComponent,
    canActivate: [AngularFireAuthGuard],
    children: [
      { path: 'log', redirectTo: '/app/log/' + (new Date()).getFullYear() + '-' + (new Date()).getMonth() + '-' + (new Date()).getDay(), pathMatch: 'full' },
      { path: 'log/:day', component: LogComponent },
      { path: 'dashboard', component: DashboardComponent },
    ]
  },


  {
    path: '',
    redirectTo: '/login',
    pathMatch: 'full'
  },

  { path: '**', component: PageNotFoundComponent },

];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
