import { Component, OnInit } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from '@angular/fire/firestore';
import { Day } from 'src/app/models/day';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import * as moment from 'moment';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {

  private dayCollection: AngularFirestoreCollection<Day>;
  days: Observable<Day[]>;
  visibleDayId = moment().format(environment.dateformat);

  constructor(
    private db: AngularFirestore
  ) {
    this.dayCollection = db.collection<Day>('days');
    this.days = this.db.collection<Day>('days').snapshotChanges().pipe(
      map(actions => actions.map(a => {
        const data = a.payload.doc.data() as Day;
        const id = a.payload.doc.id;
        return { id, ...data };
      }))
    );

  }

  ngOnInit() {

  }

  show(day: Day) {
    this.visibleDayId = day.id;
  }

}
